<template>
	<div>随机装备测试</div>
	
	<div>
		<div>参数：</div>
		<div>num:<input type="text" v-model="num" name="rdnum" id="rdnum" placeholder="num"></div>
		<div>list:<input disabled type="text" name="rdlist" id="rdlist" placeholder="list"></div>
		<div>percent:<input type="text" v-model="ppercent" name="rdpercent" id="rdpercent" placeholder="percent"></div>
		<div>limit:<input disabled type="text" name="rdlimit" id="rdlimit" placeholder="limit"></div>
		<div>level:<input disabled type="text" name="rdlevel" id="rdlevel" placeholder="level"></div>
		<div>lucky:<input type="text" v-model="llucky" name="rdlucky" id="rdlucky" placeholder="lucky"></div>
		<div><label for="rdislog"> islog: </label><input type="checkbox" v-model="iislog" name="rdislog" id="rdislog"></div>
	</div>
	<div>{{num}}</div>
	<!-- <div>{{JSON.stringify(ppercent)}}</div> -->
	<div>随机装备：{{list}}</div>
	<button @click="randomDraw(
			num=num,
			percent=JSON.parse(ppercent),
			islog=iislog,lucky=llucky)">生成武器</button>
	<input type="button" :value="num" maxlength="3">
	<button type="button" @click="num++">增加</button><button type="button" @click="num<=1?0:num--">减少</button>
	<table border="1" style="margin:0 auto">
		<tr>
			<th>级别</th>
			<td v-for="(item,index) in tblevel" :key="index" style="width;">{{item[0]}}</td>
		</tr>
		<tr>
			<th>概率</th>
			<td v-for="(item,index) in tbpercent" :key="index">{{item[1]||0}}%</td>
		</tr>
		<tr>
			<th>数量</th>
			<td v-for="(item,index) in tbcount" :key="index">{{item}}</td>
		</tr>
	</table>
	<NavBar/>
</template>

<script>
	import { ref } from 'vue';
	import NavBar from '@/components/NavBar.vue';
	
	export default {
		name: 'RandDraw',
		components:{
			NavBar
		},
		data() {
			return {
				a:0,
				num: 10,
				tblevel: new Array(6).fill("-"),
				tbpercent: new Array(6).fill("0"),
				tbcount: new Array(6).fill("0"),
				ppercent: JSON.stringify([['传承级',5,{start:0,end:8}],['完美级',45,{start:24,end:32}],['一般级',50,{start:40,end:48}]]),
				iislog: false ,
				llucky: 0,
				bak: [
					'SSS-弓箭', 'SSS-法杖', 'SSS-长剑', 'SSS-短剑', 'SSS-大刀', 'SSS-战斧', 'SSS-扇子', 'SSS-飞镖',
					'SS-弓箭', 'SS-法杖', 'SS-长剑', 'SS-短剑', 'SS-大刀', 'SS-战斧', 'SS-扇子', 'SS-飞镖',
					'S-弓箭', 'S-法杖', 'S-长剑', 'S-短剑', 'S-大刀', 'S-战斧', 'S-扇子', 'S-飞镖',
					'A-弓箭', 'A-法杖', 'A-长剑', 'A-短剑', 'A-大刀', 'A-战斧', 'A-扇子', 'A-飞镖',
					'B-弓箭', 'B-法杖', 'B-长剑', 'B-短剑', 'B-大刀', 'B-战斧', 'B-扇子', 'B-飞镖',
					'C-弓箭', 'C-法杖', 'C-长剑', 'C-短剑', 'C-大刀', 'C-战斧', 'C-扇子', 'C-飞镖',
				],
				list: ref([''])
			}
		},
		methods: {

			randomDraw(num=0, list, percent, limit, level, lucky=0, islog=false) {
				/**
				 * 随机抽奖
				 * @param	{Number}	num		需要生成的数量,默认为1
				 * @param	{Object}	list	指定抽奖列表:["物品","物品",...]
				 * @param	{Object}	percent	指定对应个概率:[["等级",概率,{start:0,end:1}],...]
				 * @param	{Object}	limit	保底限制a每m次出现n个b:[a,m,n,b]
				 * @param	{Object}	level	可指定等级,默认全包含:高级抽取
				 * @param	{Number}	lucky	记录指定等级为幸运时刻:数字,0不记录,1第一级,2第二级,...
				 * @param	{Boolean}	islog	是否记录抽奖历史:true/false
				 * @return	{Array}		void
				 * @author	BenckyC
				 * @version	22.12.11
				 * @description 
				 * 可根据概率表自动识别各分段概率，需要参数：
				 * 奖项表：按顺序排列，
				 * 概率表：根据奖项表选择各个等级的范围
				 */

				// 预处理
				num = typeof this.num === 'number' ? this.num : undefined;
				list = typeof this.list === 'object' ? this.list : undefined;
				percent = typeof this.percent === 'object' ? this.percent : undefined;
				limit = typeof this.limit === 'object' ? this.limit : undefined;
				level = typeof this.level === 'object' ? this.level : undefined;
				lucky = typeof this.lucky === 'number' ? this.lucky : undefined;
				islog = typeof this.islog === 'boolean' ? this.islog : undefined;
				console.clear();

				// 参数预览
				const isCollapse = 1;
				isCollapse ? console.groupCollapsed('参数预览：') : console.group('参数预览：')
				console.log('num:\t', num)
				console.log('list:\t', list)
				console.log('percent:', percent)
				console.log('limit:\t', limit)
				console.log('level:\t', level)
				console.log('lucky:\t', lucky)
				console.log('islog:\t', islog)
				console.groupEnd()
				
				// 存储大小
				const isCollapse2 = 1;
				isCollapse2 ? console.groupCollapsed('存储大小：') : console.group('存储大小：')
				var _lsTotal=0,_xLen,_x;
				for(_x in localStorage){
					if(!localStorage.hasOwnProperty.call(_x)){continue;}
					_xLen= ((localStorage[_x].length + _x.length)* 2);
					_lsTotal+=_xLen;
					console.log(_x.substr(0,50)+" ("+ (_xLen/1024).toFixed(2)+" KB)")
				}
				console.log("Total (" + (_lsTotal / 1024).toFixed(2) +" KB)");
				console.groupEnd()

				// 函数各模块功能
				const func = {
					num: (num, percent) => {
						// 默认概率表
						var percent_list = [
							['SSS', 0.1, {start: 0,end: 8}],
							['SS', 0.9, {start: 8,end: 16}],
							['S', 4, {start: 16,end: 24}],
							['A', 15, {start: 24,end: 32}],
							['B', 20, {start: 32,end: 40}],
							['C', 60, {start: 40,end: 48}],
						]

						// num	无数量时候默认为1
						num = num == undefined ? 1 : num;

						// percent	默认为percent_list内容
						percent_list = func.percent(percent_list, percent);

						// 临时赋值
						this.tblevel = percent_list;
						// 临时赋值
						this.tbpercent = percent_list;

						// 概率累加判断
						var percent_sum = [];
						for (var list_len = 0; list_len < percent_list.length; list_len++) {
							percent_sum.push(percent_list[list_len][1] + (list_len - 1 < 0 ? 0 : percent_sum[list_len - 1]))
						}
						percent_sum.at(-1) != 100 ? eval(
							'console.warn("[Warn]","The total percentage is "+percent_sum.at(-1)+"%, and the value is corrected.");percent_sum.splice(-1, 1, 100)'
						) : console.info("[Info]",'Total percentage correct.')
						console.log('概率总和：', percent_sum);

						// 各概率次数统计
						var sum_len = percent_list.length;
						var percent_count = new Array(percent_list.length).fill(0);

						// 传递外部参数 概率次数赋值
						this.list = [];

						// 内部统计表
						var curlist=[];
						var history_latest = [{cnt: percent_count,sum: sum_len,curlist: curlist,}]; //记录总数，cnt先赋0值，后修改
						let temp_num = sum_len //临时记录
						while (temp_num--) {history_latest.splice(1, 0, {lev: temp_num,nam: "",pct: 0,res: []});} //在第0个后面添加
						console.log('history_latest_before', history_latest);

						for (var k = 0; k < num; k++) {
							var rand_num = parseInt(Math.random() * 10000) / 100; //随机数  百分制
							let temp_i = 0; //等级序号
							while (temp_i < sum_len) {
								history_latest[temp_i + 1].nam = percent_list[temp_i][0]
								history_latest[temp_i + 1].pct = percent_list[temp_i][1]
								if (rand_num < percent_sum[temp_i]) {
									// 防止指向同一处地址——子记录
									var history_sub = {
										lev: temp_i, //等级
										nam: percent_list[temp_i][0], //名称
										pct: percent_list[temp_i][1], //概率
										res: [] //物品
									};
									var m = percent_list[temp_i][2].start
									var n = percent_list[temp_i][2].end
									percent_count[temp_i]++
									break;
								}
								temp_i++
							}
							// 物品添加
							let rand_int = Math.floor(Math.random() * (n - m) + m);
							curlist.push(this.bak[rand_int])
							history_sub.res.push(this.bak[rand_int])
							history_latest[history_sub.lev + 1].res.push(history_sub.res[0])
							this.list.push(this.bak[rand_int])

							// 列表添加记录，不存在的记录添加表头，存在的添加资源
							// if (history_latest.indexOf(history_sub.lev)==-1) {
							//	history_latest[history_sub.lev+1].res.push(history_sub.res[0])//数组转字符串
							// }else{
							// 	history_latest[history_sub.lev+1]=history_sub
							// }
						}
						// history_latest[0].cnt=percent_count
						console.log("history_latest_after", history_latest);
						window.localStorage.setItem('bk_GE_history_latest', JSON.stringify(history_latest))
						

						// 临时赋值
						this.tbcount = percent_count;
						console.log('数量总和：', percent_count.join(' '), "sum:" + eval(percent_count.join('+')))

					},
					list: () => {

					},
					percent: (percent_default, percent) => {
						var percent_list = percent == undefined ? percent_default : percent;
						while (percent_list == undefined) {
							for (var i = 0; i < percent.length; i++) {
								if (typeof percent_list[i][2].start !== 'number' || typeof percent_list[i][2]
									.end !== 'number') {
									console.error('[Error]', 'Missing object \'start\' or \'end\'.');
									percent_list = percent_default;
								}
							}
						}
						return percent_list
					},
					limit: (limit) => {
						console.log('limit', limit);
						var limit_list = [
							['C', 10, 2, 'B'],
							['B', 10, 1, 'A']
						]
						for (var i = 0; i < limit_list.length; i++) {
							limit_list[i]
						}
					},
					level: () => {

					},
					lucky: (lucky) => {
						lucky = lucky == undefined ? 0 : lucky;
						console.log("lucky",lucky);
						if (lucky!=0) {
							var bk_GE_history = JSON.parse(window.localStorage.getItem('bk_GE_history')) || [];
							console.log(bk_GE_history);
							
							window.localStorage.setItem("bk_GE_lucky",JSON.stringify("lucky"));
						}

					},
					islog: (islog) => { //, percent_list, percent_count
						var isLog = islog == undefined || islog == false ? false : true;
						// console.log('isLog', isLog)
						
						if (isLog) {
							var latest = JSON.parse(window.localStorage.getItem('bk_GE_history_latest')) || [];
							var bk_GE_history = JSON.parse(window.localStorage.getItem('bk_GE_history')) || [];
							var obj_history = {date: 0,list:[],log: []};

							for (var i = 0; i < latest[0].sum; i++) {
								obj_history.date = new Date().getTime();
								obj_history.list = latest[0].curlist;
								var obj_log = {level: "",name: "",percent: 0,count: 0,list: []};
								if (latest[i + 1] == i) {
									continue
								} else {
									obj_log.level = latest[i + 1].lev;
									obj_log.name = latest[i + 1].nam;
									obj_log.percent = latest[i + 1].pct;
									obj_log.count = latest[0].cnt[i];
									obj_log.list = latest[i + 1].res;
									obj_history.log.push(obj_log)
								}
							}

							bk_GE_history.push(obj_history);
							console.log("最新历史记录",obj_history);

							window.localStorage.setItem('bk_GE_history', JSON.stringify(bk_GE_history))
							// console.log("history: ", JSON.parse(window.localStorage.getItem('bk_GE_history')));
						}
					}
				}
				func.num(num, percent)
				func.limit(limit)
				func.lucky(lucky)
				func.islog(islog)

			},
			storeSize(){
				var _lsTotal=0,_xLen,_x;
				for(_x in localStorage){ 
					if(!localStorage.hasOwnProperty.call(_x)){continue;}
					_xLen= ((localStorage[_x].length + _x.length)* 2);
					_lsTotal+=_xLen;
					console.log(_x.substr(0,50)+" ="+ (_xLen/1024).toFixed(2)+" KB")
				}
				console.log("Total =" + (_lsTotal / 1024).toFixed(2) +" KB");
			},
			localStorageSpace:function(){
				var allStrings = '';
				for(var key in window.localStorage){
					if(window.localStorage.hasOwnProperty.call(key)){
						allStrings += window.localStorage[key];
					}
				}
				return allStrings ? 3 + ((allStrings.length*16)/(8*1024)) + ' KB' : 'Empty (0 KB)';
			}
			
		},
		setup() {
			console.log("%c Home %c https://bencky1017.github.io/ ",
				"padding: 3px; border-radius: 3px 0 0 3px; color: #fff; background: #606060;",
				"padding: 3px; border-radius: 0 3px 3px 0; color: #fff; background: #409EFF;");
			console.log("%c Made with love %c by BenckyC ",
				"padding:3px 10px;color:#fff;background:#FFAAEE;border:solid 1px #FFAAEE;",
				"padding:3px 10px;color:#444;background:#ffffff;border:solid 1px #FFAAEE;", );
			console.log("%cGithub: https://github.com/bencky1017 ",
				"padding:5px 10px;color:#000;background:#98a3ff;border:solid 1px #98a3ff;");
			console.log("%c Plugin %c https://element-plus.gitee.io/zh-CN/component/button.html ",
				"padding: 3px; border-radius: 3px 0 0 3px; color: #fff; background: #606060;",
				"padding: 3px; border-radius: 0 3px 3px 0; color: #fff; background: #79BBFF;");
			console.log(
				"%c ███████████  ██████████ ██████   █████   █████████  █████   ████ █████ █████\n▒▒███▒▒▒▒▒███▒▒███▒▒▒▒▒█▒▒██████ ▒▒███   ███▒▒▒▒▒███▒▒███   ███▒ ▒▒███ ▒▒███ \n ▒███    ▒███ ▒███  █ ▒  ▒███▒███ ▒███  ███     ▒▒▒  ▒███  ███    ▒▒███ ███  \n ▒██████████  ▒██████    ▒███▒▒███▒███ ▒███          ▒███████      ▒▒█████   \n ▒███▒▒▒▒▒███ ▒███▒▒█    ▒███ ▒▒██████ ▒███          ▒███▒▒███      ▒▒███    \n ▒███    ▒███ ▒███ ▒   █ ▒███  ▒▒█████ ▒▒███     ███ ▒███ ▒▒███      ▒███    \n ███████████  ██████████ █████  ▒▒█████ ▒▒█████████  █████ ▒▒████    █████   \n▒▒▒▒▒▒▒▒▒▒▒  ▒▒▒▒▒▒▒▒▒▒ ▒▒▒▒▒    ▒▒▒▒▒   ▒▒▒▒▒▒▒▒▒  ▒▒▒▒▒   ▒▒▒▒    ▒▒▒▒▒    ",
				"color: #bebebe","rebel");
		}
	}
</script>


<style>
	.logo {
		height: 6em;
		padding: 1.5em;
		will-change: filter;
	}
	
	.logo:hover {
		filter: drop-shadow(0 0 2em #646cffaa);
	}
	
	.logo.vue:hover {
		filter: drop-shadow(0 0 2em #42b883aa);
	}
	
	
	.inputnum {
		height: 36px;
		width: 80px;
		font-size: 20px;
	}
</style>